import { renderComponent } from './react-dom';

// Â∞Ü‰∏äÊñáÂÆö‰πâÁöÑcreateElementÊñπÊ≥ïÊîæÂà∞ÂØπË±°React‰∏≠
function createElement(tag, attrs, ...children) {
  return {
    tag,
    attrs,
    children,
  };
}

class Component {
  constructor(props) {
    this.state = {};
    this.props = props;
  }

  setState(stateChange) {
    // ÂêàÂπ∂stateChangeÂà∞state‰∏≠
    Object.assign(this.state, stateChange);

    renderComponent(this);
  }
}

const React = {
  createElement,
  Component
};


export default React;
